import React from "react";
import Styled from "styled-components";

// Images
import Gatsby from "../../img/Gatsby-Logo.svg";
import Typescript from "../../img/Typescript-Logo.svg";

const Backdrop = Styled.div`
position: relative;
`;

const BackdropBoxContainer = Styled.div`
position: fixed;
top: 30%;
left: 10%;
width: 400px;
height: 400px;
/* inset: 30% 80% 30% 10%; */
/* transform: rotate(30deg); */
transform: rotate3d(1, 1, 1, 30deg);
transform-origin: bottom; // Mobile!
transform-style: preserve-3d;
`;

const BackdropBox = Styled.div`
position: absolute;
bottom: 0;
height: 100px;
width: 100%;

background-color: firebrick;
border-radius: 5px;
opacity: 90%;
`;

const BackdropBoxFront = Styled(BackdropBox)`
`;

const BackdropBoxBack = Styled(BackdropBox)`
transform: translateZ(75px);
`;

const BackdropBoxBottom = Styled(BackdropBox)`
height: 75px;
transform-origin: bottom;
transform: rotateX(-90deg);
`;

const BackdropBoxLeft = Styled(BackdropBox)`
transform-origin: left;
left: 0;
width: 75px;
transform: rotateY(-90deg);
`;

const BackdropBoxRight = Styled(BackdropBox)`
transform-origin: right;
right: 0;
width: 75px;
transform: translateX(20px) translateY(-30px) rotateY(-90deg);
`;

const BackdropSpinner = Styled.div`
animation: 50s linear infinite BackdropSpinner;
position: absolute;
bottom: 0;
width: 100%;
height: 100%;
`;

const BackdropIcon = Styled.i`
height: 40px;
width: 40px;
margin: 0;

  --az: calc(var(--i)*1turn/var(--m));
  transform: 
    rotate(var(--az)) 
    translate(var(--r))
    rotate(calc(-1*var(--az)));
`;

const BackdropImage = Styled.img`
height: 40px;
width: 40px;

  --az: calc(var(--i)*1turn/var(--m));
  transform: 
    rotate(var(--az)) 
    translate(var(--r))
    rotate(calc(-1*var(--az)));
`;

type MainBodyProps = {
  children: JSX.Element | JSX.Element[];
  noBackdrop?: boolean;
};

type DefaultLayoutProps = MainBodyProps & {};

// const skillIcons = [
//   <BackdropIcon key="react" className="fab fa-react" sort={0} />,
//   <BackdropImage key="gatsby" src={Gatsby} alt="Gatsby" sort={1} />,
//   <BackdropIcon key="html5" className="fab fa-html5" sort={2} />,
//   <BackdropIcon key="css3" className="fab fa-css3" sort={3} />,
//   <BackdropIcon key="js" className="fab fa-js-square" sort={4} />,
//   <BackdropImage key="typescript" src={Typescript} alt="Typescript" sort={5} />,
//   <BackdropIcon key="linux" className="fab fa-linux" sort={6} />
// ];
const icons: {
  key: string;
  sort: number;
  className?: string;
  src?: JSX.Element;
  alt?: string;
}[] = [
  { key: "react", className: "fab fa-react", sort: 0 },
  { key: "gatsby", src: Gatsby, alt: "Gatsby", sort: 1 },
  { key: "html5", className: "fab fa-html5", sort: 2 },
  { key: "css3", className: "fab fa-css3", sort: 3 },
  { key: "js", className: "fab fa-js-square", sort: 4 },
  { key: "typescript", src: Typescript, alt: "Typescript", sort: 5 },
  { key: "linux", className: "fab fa-linux", sort: 6 }
];

const DefaultLayout = ({ children }: DefaultLayoutProps): JSX.Element => {
  const n = icons.length;
  const tan: number = Math.tan(Math.PI / 6);
  const r: number = 0.5 / tan;

  return (
    <Backdrop>
      {/* <BackdropBoxContainer style={`--m: ${n}; --tan: ${+tan.toFixed(2)}`}> */}
      <BackdropBoxContainer style={{ "--m": n, "--tan": tan.toFixed(2) }}>
        <BackdropBoxFront />
        <BackdropBoxLeft />
        <BackdropBoxRight />
        <BackdropSpinner>
          {icons.map(icon =>
            icon?.src ? (
              <BackdropImage key={icon.key} style={`--i: ${icon.sort}`} />
            ) : (
              <BackdropIcon key={icon.key} style={`--i: ${icon.sort}`} />
            )
          )}
        </BackdropSpinner>
        <BackdropBoxBack />
        <BackdropBoxBottom />
      </BackdropBoxContainer>
      {children}
    </Backdrop>
  );
};

export default DefaultLayout;
